import React from "react";
import PropTypes from "prop-types";
import ProgressBar from "react-bootstrap/ProgressBar";
import data from "../../data/inner-pages/aboutuspage.json";

export default function BusinessTime() {
    const now = 60;
    return (
        <>
            <div className="business-time container">
                <div className="d-flex flex-row flex-wrap">
                    <div className="business-sections col-lg-6 col-sm-6">
                        <h5>{data.visitsubtitle}</h5>
                        <h2 className="web-subtitle">{data.visittime}</h2>
                    </div>

                    <div className="business-time-inner col-lg-6 col-sm-6">
                        <p>{data.officetime}</p>
                        <div className=" timing-section">
                            <div className="business-day">{data.days} </div>
                            <div className="business-time">
                                {data.opentimings} - {data.closeimings}
                            </div>
                        </div>

                        <div className="days-contaner">
                            <span className="business-day">
                                {data.openday}{" "}
                            </span>
                            <span className="business-closed">
                                {data.closeday}
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

BusinessTime.propTypes = {
    visitsubtitle: PropTypes.string,
    visittime: PropTypes.string,
    officetime: PropTypes.string,
    days: PropTypes.string,
    opentimings: PropTypes.string,
    closeimings: PropTypes.string,
    openday: PropTypes.string,
    closeday: PropTypes.string,
};
