import React from "react";
import PropTypes from "prop-types";
import { Accordion } from "react-bootstrap";
import FaqList from "../../components/faq";
import data from "../../data/inner-pages/aboutuspage.json";

export default function AboutUsAccordion() {
    return (
        <div className="faq-section">
            <div className="container">
                <h2>{data.faqTitle}</h2>
                {data.accordion.map((accordionsItem, i) => {
                    return (
                        <Accordion
                            key={i}
                            defaultactivekey={i}
                            className="accordion-item"
                        >
                            <FaqList
                                title={accordionsItem.accordionTitle}
                                description={
                                    accordionsItem.accordionDescription
                                }
                            />
                        </Accordion>
                    );
                })}
            </div>
        </div>
    );
}

AboutUsAccordion.propTypes = {
    faqTitle: PropTypes.string,
    accordionTitle: PropTypes.string,
    accordionDescription: PropTypes.string,
};
