import React from "react";
import ProgressBar from "react-bootstrap/ProgressBar";
import PropTypes from "prop-types";
import data from "../../data/progressbar/data.json";

export default function ProgressBars() {
    return (
        <>
            <div className="container d-flex flex-wrap ">
                <div className="skills-bar-container col-lg-6 col-md-6 col-sm-12">
                    {data.progressbar.map((item) => {
                        return (
                            <div className="bar-div">
                                <h6>{item.title}</h6>
                                <ProgressBar
                                    now={item.now}
                                    label={item.lebel}
                                />
                            </div>
                        );
                    })}
                </div>
                <div className="skills-container col-lg-6 col-md-6 col-sm-12">
                    <h5>{data.subtitle}</h5>
                    <h2>{data.title}</h2>
                    <p>{data.description}</p>
                </div>
            </div>
        </>
    );
}

ProgressBars.propTypes = {
    title: PropTypes.string,
    lebel: PropTypes.string,
};
