import React, { useEffect } from "react";
import counterdata from "../../../data/homepages/counter.json";

export default function Logo() {
    useEffect(() => {
        class NumberUpdater {
            constructor(container) {
                this.container = document.querySelector(
                    `[data-number-adder-name="${container}"]`
                );
                this.init = this.init.bind(this);
                this.getCurrentItem = this.getCurrentItem.bind(this);
                this.getFilteredItem = this.getFilteredItem.bind(this);
                this.setObjectItems = this.setObjectItems.bind(this);
                this.getObjectItems = this.getObjectItems.bind(this);
                this.updateFrameItems = this.updateFrameItems.bind(this);
            }

            getFilteredItem = (item) => {
                const isStartNumber =
                    item.getAttribute("data-number-adder") === "number";

                return isStartNumber ? item : console.log("Verifique seu HTML");
            };

            getCurrentItem = (childrens) => {
                return Array.from(childrens).filter(this.getFilteredItem)[0];
            };

            setObjectItems = (item) => ({
                item: this.getCurrentItem(item.children),
                currentValue: item.getAttribute("data-number-adder-current"),
                nextValue: item.getAttribute("data-number-adder-next"),
            });

            getObjectItems = (container) => {
                return Array.from(container.children, this.setObjectItems);
            };

            updateFrameItems = ({ item, currentValue, nextValue }) => {
                const updateContent = () => {
                    if (currentValue < nextValue) {
                        currentValue++;
                        item.textContent = currentValue;
                        requestAnimationFrame(updateContent);
                    }
                    cancelAnimationFrame(updateContent);
                };

                requestAnimationFrame(updateContent);
            };

            init = () => {
                this.getObjectItems(this.container).map(this.updateFrameItems);
            };
        }

        const numberUpdater0 = new NumberUpdater("container-0");

        numberUpdater0.init();
    }, []);
    return (
        <section className="section container">
            <ul data-number-adder-name="container-0">
                <li data-number-adder-current="0" data-number-adder-next="100">
                    <span data-number-adder="number"> {counterdata.zero}</span>
                    {counterdata.percentile}
                    <br />
                    <div className="counte-title">
                        <h6>{counterdata.titleone}</h6>
                    </div>
                </li>
                <li data-number-adder-current="0" data-number-adder-next="100">
                    <span data-number-adder="number"> {counterdata.zero}</span>
                    {counterdata.plus}
                    <br />
                    <div className="counte-title">
                        <h6>{counterdata.titletwo}</h6>
                    </div>
                </li>
                <li data-number-adder-current="0" data-number-adder-next="10">
                    <span data-number-adder="number"> {counterdata.zero}</span>
                    <br />
                    <div className="counte-title">
                        <h6>{counterdata.titlethree}</h6>
                    </div>
                </li>
                <li data-number-adder-current="0" data-number-adder-next="20">
                    <span data-number-adder="number"> {counterdata.zero}</span>
                    {counterdata.plus}
                    <br />
                    <div className="counte-title">
                        <h6>{counterdata.titlefour}</h6>
                    </div>
                </li>
            </ul>
        </section>
    );
}
